.document-info-item {
  position: relative;
  display: flex;
  align-items: center;
  min-height: $input-height;
  padding: 5px 10px 5px 50px;
  border: 2px solid $blue-light;
  border-radius: $border-radius;

  &:before {
    @include size(28px);
    @include inline-flex-center;

    position: absolute;
    top: 50%;
    left: 10px;
    z-index: 5;
    font-size: 16px;
    font-family: "icomoon";
    transform: translate(0, -50%);
    content: "\e914";
  }

  &__text {
    margin-bottom: 0;
    color: $blue-lake-ice;
    text-decoration: underline;
    text-decoration-thickness: 1px;
    text-underline-offset: 3px;
  }

  &__action-btn {
    @include btn-reset;
    @include size(30px);
    @include inline-flex-center;

    font-size: 18px;
    transition: color 0.3s;

    &:hover {
      color: $primary;
    }
  }
}
