.select2-container {
  &--bootstrap {
    display: block;
    width: auto !important;

    &.select2-container--open {
      .select2-selection {
        &.select2-selection--multiple {
          .select2-selection__choice {
            &:last-child {
              &:after {
                display: inline-block;
                content: ",\00a0";
              }
            }
          }
        }
      }
    }

    &.select2-container--focus {
      .select2-selection {
        border-color: $form-select-focus-border-color;
        box-shadow: $form-select-focus-box-shadow;
      }
    }

    .select2-tag-text {
      flex-shrink: 1;
      max-width: 100%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .select2-tag {
      display: inline-flex;
      padding: 2px 10px;
      font-weight: $font-weight-bold;
      font-size: $font-size-sm;
      background-color: $blue-light;
      border-radius: 3px;
    }

    .select2-selection {
      height: $input-height;
      padding: $form-select-padding-y $form-select-indicator-padding $form-select-padding-y $form-select-padding-x;
      color: $form-select-color;
      font-weight: $form-select-font-weight;
      font-size: $form-select-font-size;
      font-family: $form-select-font-family;
      line-height: $form-select-line-height;
      background-color: $form-select-bg;
      border: $form-select-border-width solid $form-select-border-color;
      border-radius: $form-select-border-radius;
      transition: $form-select-transition;

      &--multiple {
        min-height: auto;

        &:after {
          position: absolute;
          top: 50%;
          right: $form-select-padding-x;
          display: block;
          width: nth(split-string(#{$form-select-bg-size}, " "), 1);
          height: nth(split-string(#{$form-select-bg-size}, " "), 2);
          background-image: escape-svg($form-select-indicator);
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          transform: translate(0, -50%);
          transition: transform 0.3s;
          content: "";
        }

        .select2-search {
          display: inline-block;
          margin: 0;

          &__field {
            @include placeholder {
              color: $input-placeholder-color;
              line-height: $input-line-height;
            }

            height: $input-font-size * $input-line-height;
            margin: 0;
            color: $input-color;
            font-weight: $input-font-weight;
            font-size: $input-font-size;
            font-family: inherit;
            line-height: $input-line-height;
            background-image: none;
            caret-color: $input-color;
            cursor: pointer;
          }
        }

        .select2-selection__choice {
          display: inline-block;

          &:not(:last-child) {
            &:after {
              display: inline-block;
              content: ",\00a0";
            }
          }
        }

        /* stylelint-disable-next-line selector-class-pattern */
        .select2-selection__choice__remove {
          display: none;
        }
      }

      &--clearable {
        padding-right: calc($form-select-indicator-padding + 20px);
      }

      &[aria-disabled="true"] {
        color: $form-select-disabled-color;
        background-color: $form-select-disabled-bg;
        border-color: $form-select-disabled-border-color;
        cursor: not-allowed;
      }

      &__placeholder {
        color: $input-placeholder-color;
      }

      &__rendered {
        padding: 0 !important;
      }

      &__arrow {
        position: absolute;
        top: 50%;
        right: $form-select-padding-x;
        width: nth(split-string(#{$form-select-bg-size}, " "), 1);
        height: nth(split-string(#{$form-select-bg-size}, " "), 2);
        background-image: escape-svg($form-select-indicator);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transform: translate(0, -50%);
      }

      &__clear {
        position: absolute;
        top: 50%;
        right: 35px;
        z-index: 5;
        display: inline-flex;
        padding: 0;
        font-size: 14px !important;
        transform: translate(0, -50%);
        transition: color 0.3s;

        &:hover {
          color: $danger;
        }

        &:after {
          font-family: "icomoon";
          content: "\e90f";
        }

        span {
          font-size: 0;
        }
      }
    }

    .select2-dropdown {
      z-index: $zindex-popover;
      overflow: hidden;
      background-color: $form-select-bg;
      border: $form-select-border-width solid $form-select-focus-border-color;
      border-radius: $form-select-border-radius;

      &--above {
        transform: translateY(-2px);

        &.bordered-version {
          transform: translateY(-6px);
        }
      }

      &--below {
        transform: translateY(2px);

        &.bordered-version {
          transform: translateY(6px);
        }
      }

      &.checkbox-view {
        .select2-results {
          &__option {
            &:not(.select2-results__message) {
              position: relative;
              padding-left: 28px;

              &:hover {
                background-color: $blue-light !important;
              }

              &.select2-results__option--selected {
                background-color: transparent;

                &:before {
                  font-size: 5px;
                  background-color: $form-check-input-checked-bg-color;
                  border-color: $form-check-input-checked-bg-color;
                }
              }

              &:before {
                @include size(10px);
                @include inline-flex-center;

                position: absolute;
                top: 8px;
                left: 10px;
                z-index: 5;
                color: $blue-light;
                font-size: 0;
                font-family: "icomoon";
                border: 1px solid $secondary;
                border-radius: 1px;
                content: "\e90d";
              }
            }
          }
        }
      }

      &.pagination-version {
        .select2-results {
          &__options {
            padding: 0;
          }

          &__option {
            padding: 10px 24px;
          }
        }
      }
    }

    .select2-results {
      @extend %custom-scrollbar-thin;

      max-height: 300px;
      overflow: auto;

      &__options {
        padding: 10px 0;
      }

      &__option {
        padding: 5px 10px;
        color: $secondary;
        font-weight: $font-weight-bold;
        font-size: $font-size-sm;
        font-family: $form-select-font-family;
        line-height: $form-select-line-height;
        transition:
          background-color 0.3s,
          color 0.3s;

        &:not(.select2-results__message, .select2-results__option--selected) {
          &:hover {
            background-color: $light;
          }
        }

        &.select2-results__option--selected {
          background-color: $blue-light;
        }
      }

      &__message {
        padding: 0.25rem 0.5rem;
        color: $gray;
        text-align: center;
      }
    }

    .select2-search {
      &.select2-search--dropdown {
        padding: 0.5rem;

        .select2-search__field {
          @include placeholder {
            color: $input-placeholder-color;
          }

          padding: $input-padding-y $input-padding-x;
          color: $input-color;
          font-weight: $input-font-weight;
          font-size: $input-font-size;
          font-family: $input-font-family;
          line-height: $input-line-height;
          background-color: $input-bg;
          border: $input-border-width solid $input-border-color;
          border-radius: $input-border-radius;
          transition: $input-transition;

          &:focus {
            color: $input-focus-color;
            background-color: $input-focus-bg;
            border-color: $input-focus-border-color;
            box-shadow: $input-focus-box-shadow;
          }
        }
      }
    }
  }
}

.form-select {
  &.is-valid {
    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          color: $form-valid-color;
          border-color: $form-valid-border-color;
        }
      }
    }
  }

  &.is-invalid {
    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          color: $form-invalid-color;
          border-color: $form-invalid-border-color;
        }
      }
    }
  }

  &.is-selected {
    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          &__arrow {
            background-image: escape-svg($form-select-indicator-active);
          }

          &--multiple {
            &:after {
              background-image: escape-svg($form-select-indicator-active);
            }
          }
        }
      }
    }
  }

  &.form-select-sm {
    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          padding: $form-select-padding-y-sm $form-select-indicator-padding $form-select-padding-y-sm $form-select-padding-x-sm;
          font-size: $form-select-font-size-sm;
          border-radius: $form-select-border-radius-sm;

          &__arrow {
            right: $form-select-padding-x-sm;
          }
        }

        .select2-dropdown {
          border-radius: $form-select-border-radius-sm;
        }

        .select2-results {
          &__option {
            padding: $form-select-padding-y-sm $form-select-padding-x-sm;
            font-size: $form-select-font-size-sm;
          }
        }
      }
    }
  }

  &.form-select-lg {
    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          padding: $form-select-padding-y-lg $form-select-indicator-padding $form-select-padding-y-lg $form-select-padding-x-lg;
          font-size: $form-select-font-size-lg;
          border-radius: $form-select-border-radius-lg;

          &__arrow {
            right: $form-select-padding-x-lg;
          }
        }

        .select2-dropdown {
          border-radius: $form-select-border-radius-lg;
        }

        .select2-results {
          &__option {
            padding: $form-select-padding-y-lg $form-select-padding-x-lg;
            font-size: $form-select-font-size-lg;
          }
        }
      }
    }
  }

  &.bordered-version {
    height: auto;
    padding-top: 4px;
    padding-bottom: 4px;
    color: $input-placeholder-color;
    font-weight: $font-weight-normal;
    font-size: 16px;
    background-color: transparent;
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 0;

    @include media-breakpoint-up(lg) {
      font-size: 20px;
    }

    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          height: auto;
          padding-top: 4px;
          padding-bottom: 4px;
          font-size: 16px;
          background-color: transparent;
          border-top: none;
          border-right: none;
          border-left: none;
          border-radius: 0;

          @include media-breakpoint-up(lg) {
            font-size: 20px;
          }
        }
      }
    }

    &.white-border {
      border-color: $white;

      ~ .select2-container {
        &--bootstrap {
          .select2-selection {
            border-color: $white;
          }
        }
      }
    }
  }

  &.dark-version {
    background-color: transparent;
    border-color: $secondary;

    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          background-color: transparent;
          border-color: $secondary;

          &__placeholder {
            color: $secondary;
          }

          &__arrow {
            background-image: escape-svg($form-select-indicator-active);
          }
        }
      }
    }
  }

  &.pagination-version {
    min-width: 146px;
    height: 56px;
    padding: 10px 47px 10px 24px;
    font-weight: $font-weight-bold;
    background-color: $blue-light;
    background-position: right 24px center;

    &:hover {
      border-color: $primary;
    }

    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          display: flex;
          align-items: center;
          min-width: 146px;
          height: 56px;
          padding: 10px 47px 10px 24px;
          font-weight: $font-weight-bold;
          background-color: $blue-light;

          &:hover {
            border-color: $primary;
          }

          &__arrow {
            right: 24px;
          }
        }
      }
    }
  }
}

.select2-hidden-accessible {
  @include visually-hidden;

  top: 0;
}

.table {
  thead {
    th {
      .form-select {
        &.bordered-version {
          ~ .select2-container {
            &--bootstrap {
              max-width: 190px;

              .select2-selection {
                font-weight: $font-weight-bold;

                &__placeholder {
                  font-weight: $font-weight-normal;
                }
              }
            }
          }
        }
      }
    }
  }
}
