.modal {
  @extend %custom-scrollbar;

  &.without-body-padding {
    .modal-body {
      padding-right: $modal-header-padding-x;
      padding-left: $modal-header-padding-x;
    }
  }

  &-dialog {
    padding-right: $modal-dialog-margin;
    padding-left: $modal-dialog-margin;

    @include media-breakpoint-down(sm) {
      margin-right: 0;
      margin-left: 0;
    }
  }

  &-header {
    .btn-close {
      margin: 0 0 0 auto;
      padding: 0;
    }
  }

  &-body {
    padding-top: 10px;
    padding-bottom: 20px;
    font-size: $font-size-base;

    @include media-breakpoint-down(xs) {
      padding-right: $modal-header-padding-x;
      padding-left: $modal-header-padding-x;
    }
  }

  &-footer {
    justify-content: flex-start;
    padding: 10px $modal-header-padding-x 20px;
  }
}
