.nav {
  &-underline {
    @include custom-scrollbar(0, transparent, transparent);

    flex-wrap: nowrap;
    overflow-x: auto;

    .nav-link {
      padding-right: 25px;
      padding-left: 25px;
      font-weight: $font-weight-bold;
      white-space: nowrap;

      @include media-breakpoint-down(lg) {
        font-size: 13px;
      }

      &:not(.active) {
        border-bottom-color: $blue-light;

        &:hover {
          color: $nav-link-color;
          background-color: $blue-light;
        }
      }

      &.active {
        color: $nav-link-color;
        border-bottom-color: $nav-underline-link-active-color;
      }
    }
  }
}
