.name-item {
  position: relative;
  display: inline-flex;
  align-items: center;
  min-height: 40px;
  padding-left: 50px;

  &__round-badge {
    @include size(40px);
    @include inline-flex-center;

    position: absolute;
    top: 50%;
    left: 0;
    z-index: 5;
    font-weight: $font-weight-bold;
    font-size: 14px;
    background-color: $blue-light;
    border-radius: 50rem;
    transform: translate(0, -50%);

    @include media-breakpoint-up(lg) {
      font-size: 20px;
    }
  }

  &__text {
    @include ellipsis;

    max-width: 300px;
    margin-bottom: 0;
    font-weight: $font-weight-bold;
    font-size: 14px;

    @include media-breakpoint-up(lg) {
      font-size: 20px;
    }
  }
}
