.photo-upload {
  position: relative;
  display: inline-flex;
  overflow: hidden;
  border-radius: 50rem;

  &:hover {
    .photo-upload__icon {
      color: $primary;
    }
  }

  &.lg {
    .photo-upload__content {
      @include size(150px);
    }
  }

  &__input {
    @include absolute-cover;

    z-index: 5;
    font-size: 0;
    border-radius: 50rem;
    cursor: pointer;
    opacity: 0;
  }

  &__content {
    @include size(95px);
    @include inline-flex-center;

    background-color: $white;
    border: $border-width $border-style $border-color;
    border-radius: 50rem;
  }

  &__icon {
    color: $secondary;
    font-size: 33px;
    line-height: 1;
    transition: color 0.3s;
  }

  &__photo {
    @include absolute-cover;

    z-index: 10;
    object-fit: cover;
  }
}

.photo-upload-animal {
  position: relative;
  max-width: 285px;
  overflow: hidden;
  border-radius: $border-radius;

  &:hover {
    .photo-upload-animal__content {
      background-color: rgba($secondary, 0.5);

      &:after {
        opacity: 1;
      }
    }

    .photo-upload-animal__icon {
      color: rgba($secondary, 0.5);
    }
  }

  &__input {
    @include absolute-cover;

    z-index: 5;
    font-size: 0;
    border-radius: $border-radius;
    cursor: pointer;
    opacity: 0;
  }

  &__content {
    @include responsive-ratio(1, 1);

    position: relative;
    background-color: $primary;
    border-radius: $border-radius;
    transition: background-color 0.3s;

    &:after {
      position: absolute;
      right: 25px;
      bottom: 15px;
      z-index: 5;
      color: $white;
      font-size: 20px;
      font-family: "icomoon";
      line-height: 1;
      opacity: 0;
      transition: opacity 0.3s;
      content: "\e906";
      pointer-events: none;
    }
  }

  &__icon {
    @include absolute-center;

    color: $blue-light;
    font-size: 158px;
    line-height: 1;
    transition: color 0.3s;
  }

  &__photo {
    @include absolute-cover;

    z-index: 10;
    object-fit: cover;
  }
}
