.form-control {
  &.datepicker {
    padding-right: 40px;
    background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 11 13' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M9.8002 11.3H1.4002V4.7H9.8002M8.0002 0.5V1.7H3.2002V0.5H2.0002V1.7H1.4002C0.734195 1.7 0.200195 2.234 0.200195 2.9V11.3C0.200195 11.6183 0.326623 11.9235 0.551667 12.1485C0.776711 12.3736 1.08194 12.5 1.4002 12.5H9.8002C10.1185 12.5 10.4237 12.3736 10.6487 12.1485C10.8738 11.9235 11.0002 11.6183 11.0002 11.3V2.9C11.0002 2.58174 10.8738 2.27652 10.6487 2.05147C10.4237 1.82643 10.1185 1.7 9.8002 1.7H9.2002V0.5M8.6002 7.1H5.6002V10.1H8.6002V7.1Z' fill='#{$secondary}'/></svg>"));
    background-repeat: no-repeat;
    background-position: right $input-padding-x center;
    background-size: 10px;
  }
}

.air-datepicker {
  --adp-font-family: inherit;
  --adp-font-size: 14px;
  --adp-width: 306px;
  --adp-z-index: #{$zindex-popover};
  --adp-padding: 24px;
  --adp-grid-areas: "nav" "body" "timepicker" "buttons";
  --adp-transition-duration: 0.3s;
  --adp-transition-ease: ease-out;
  --adp-transition-offset: 8px;
  --adp-background-color: #{$white};
  --adp-background-color-hover: #{$blue-light};
  --adp-background-color-active: #eaeaea;
  --adp-background-color-in-range: rgb(92 196 239 / 0.1);
  --adp-background-color-in-range-focused: rgb(92 196 239 / 0.2);
  --adp-background-color-selected-other-month-focused: #8ad5f4;
  --adp-background-color-selected-other-month: #a2ddf6;
  --adp-color: #{$secondary};
  --adp-color-secondary: #9c9c9c;
  --adp-accent-color: #{$primary};
  --adp-color-current-date: var(--adp-accent-color);
  --adp-color-other-month: #dedede;
  --adp-color-disabled: #aeaeae;
  --adp-color-disabled-in-range: #939393;
  --adp-color-other-month-hover: #c5c5c5;
  --adp-border-color: #dbdbdb;
  --adp-border-color-inner: #efefef;
  --adp-border-radius: #{$border-radius};
  --adp-border-color-inline: #{$border-color};
  --adp-nav-height: 16px;
  --adp-nav-arrow-color: #{$gray};
  --adp-nav-action-size: 16px;
  --adp-nav-color-secondary: var(--adp-color-secondary);
  --adp-day-name-color: #{$gray};
  --adp-day-name-color-hover: #8ad5f4;
  --adp-day-cell-width: 1fr;
  --adp-day-cell-height: 30px;
  --adp-month-cell-height: 42px;
  --adp-year-cell-height: 56px;
  --adp-pointer-size: 10px;
  --adp-poiner-border-radius: 2px;
  --adp-pointer-offset: 14px;
  --adp-cell-border-radius: 50rem;
  --adp-cell-background-color-hover: var(--adp-background-color-hover);
  --adp-cell-background-color-selected: #{$primary};
  --adp-cell-background-color-selected-hover: #{$primary};
  --adp-cell-background-color-in-range: rgb(92 196 239 / 0.1);
  --adp-cell-background-color-in-range-hover: rgb(92 196 239 / 0.2);
  --adp-cell-border-color-in-range: var(--adp-cell-background-color-selected);
  --adp-btn-height: 32px;
  --adp-btn-color: var(--adp-accent-color);
  --adp-btn-color-hover: var(--adp-color);
  --adp-btn-border-radius: var(--adp-border-radius);
  --adp-btn-background-color-hover: var(--adp-background-color-hover);
  --adp-btn-background-color-active: var(--adp-background-color-active);
  --adp-time-track-height: 1px;
  --adp-time-track-color: #dedede;
  --adp-time-track-color-hover: #b1b1b1;
  --adp-time-thumb-size: 12px;
  --adp-time-padding-inner: 10px;
  --adp-time-day-period-color: var(--adp-color-secondary);
  --adp-mobile-font-size: 16px;
  --adp-mobile-nav-height: 40px;
  --adp-mobile-width: 320px;
  --adp-mobile-day-cell-height: 38px;
  --adp-mobile-month-cell-height: 48px;
  --adp-mobile-year-cell-height: 64px;

  /* stylelint-disable */
  border: $border-width $border-style $border-color;
  box-shadow: none;

  &.-from-bottom- {
    margin-top: -8px;
  }

  &.-from-top- {
    margin-bottom: -8px;
  }

  &--pointer {
    display: none;
  }

  &-nav {
    border-bottom: none;
    padding-bottom: 0;

    &--title {
      font-size: $font-size-base;
      font-weight: $font-weight-bold;
      transition: background-color 0.3s, color 0.3s;
    }

    &--action {
      height: var(--adp-nav-action-size);
      font-size: 10px;
      color: var(--adp-nav-arrow-color);
      transition: background-color 0.3s, color 0.3s;

      &:hover {
        color: $secondary;
      }
    }
  }

  &-cell {
    transition: background-color 0.3s, color 0.3s;
    font-weight: $font-weight-bold;

    &.-current- {
      &:hover {
        color: $primary;
      }
    }
  }

  &-body {
    &--day-names {
      margin-bottom: 20px;
      margin-top: 0;
    }

    &--day-name {
      font-size: $font-size-xs;
      padding: 4px;
    }

    &--cells {
      &.-days- {
        gap: 8px;
      }
    }
  }
  /* stylelint-enable */
}

.air-datepicker-overlay {
  --adp-overlay-background-color: rgb(0 0 0 / 0.3);
  --adp-overlay-transition-duration: 0.3s;
  --adp-overlay-transition-ease: ease-out;
  --adp-overlay-z-index: 99;
}
