@if $use-title-palette {
  @include title-sizes;
}

.text-block,
.remove-last-child-margin {
  > * {
    &:last-child {
      margin-bottom: 0 !important;
    }
  }

  &.row {
    > * {
      &.col,
      &[class^="col-"] {
        &:last-child {
          > * {
            margin-bottom: 0 !important;
          }
        }
      }
    }
  }
}

.text-xs {
  font-size: $font-size-xs !important;
  line-height: $line-height-xs !important;
}

.text-sm {
  font-size: $font-size-sm !important;
  line-height: $line-height-sm !important;
}

.text-base {
  font-size: $font-size-base !important;
  line-height: $line-height-base !important;
}

.text-lg {
  font-size: 14px !important;
  line-height: $line-height-lg !important;

  @include media-breakpoint-up(lg) {
    font-size: $font-size-lg !important;
  }
}

.text-xl {
  font-size: 18px !important;
  line-height: $line-height-xl !important;

  @include media-breakpoint-up(lg) {
    font-size: $font-size-xl !important;
  }
}

.text-with-icon {
  position: relative;
  padding-left: 1.875em;

  [class^="icomoon-"] {
    position: absolute;
    top: 0.05em;
    left: 0;
    z-index: 5;
    font-size: 1.25em;
  }
}

.list-with-dot {
  @include list-reset;

  li {
    position: relative;
    padding-left: 2rem;

    &:not(:last-child) {
      margin-bottom: 5px;
    }

    &:before {
      @include size(4px);

      position: absolute;
      top: 0.5em;
      left: 0.75rem;
      z-index: 5;
      background-color: currentColor;
      border-radius: $border-radius-pill;
      content: "";
    }
  }
}

.list-with-check {
  @include list-reset;

  li {
    position: relative;
    padding-left: 25px;

    &:not(:last-child) {
      margin-bottom: 5px;
    }

    &:before {
      position: absolute;
      top: 4px;
      left: 0;
      z-index: 5;
      color: $blue-lake-ice;
      font-size: 0.7em;
      font-family: "icomoon";
      content: "\e90d";
    }
  }
}

.list-with-info {
  @include list-reset;

  li {
    position: relative;
    padding-left: 25px;

    &:not(:last-child) {
      margin-bottom: 5px;
    }

    &:before {
      position: absolute;
      top: 2px;
      left: 0;
      z-index: 5;
      color: $danger;
      font-size: 0.9em;
      font-family: "icomoon";
      content: "\e912";
    }
  }
}
