.badge {
  display: inline-flex;
  gap: 8px;
  align-items: center;
  min-height: 37px;

  @include media-breakpoint-down(lg) {
    font-size: 14px;
  }

  &.sm {
    min-height: 31px;
    font-size: 13px;

    @include media-breakpoint-up(sm) {
      min-height: 33px;
      font-size: 16px;
    }
  }

  [class*="icomoon"] {
    font-size: 15px;
  }

  &-unit {
    position: relative;
    display: inline-flex;
    align-items: center;
    min-height: 30px;
    padding: 2px 30px 2px 10px;
    color: $secondary;
    font-weight: $font-weight-bold;
    font-size: 16px;
    border: $border-width $border-style $border-color;
    border-radius: $border-radius;

    &__delete-btn {
      @include btn-reset;

      position: absolute;
      top: 50%;
      right: 10px;
      z-index: 5;
      display: inline-flex;
      color: $primary;
      font-size: 12px;
      line-height: 1;
      transform: translate(0, -50%);
      transition: color 0.3s;

      &:hover {
        color: $danger;
      }
    }

    &.clear-all {
      position: relative;
      padding-right: 10px;
      padding-left: 30px;
      font-weight: $font-weight-normal;
      border: none;

      .badge-unit__delete-btn {
        right: auto;
        left: 10px;
        color: $danger;

        &:hover {
          color: darken($danger, 10%);
        }
      }
    }
  }
}
