.warning-block {
  padding: 20px;
  background-color: $warning;
  border-radius: $border-radius;

  &__title {
    position: relative;
    padding-left: 45px;
    font-weight: $font-weight-normal;

    &:before {
      position: absolute;
      top: -4px;
      left: 0;
      z-index: 5;
      display: inline-block;
      font-size: 26px;
      font-family: "icomoon";
      content: "\e904";
    }
  }
}
