.toast-container {
  position: fixed;
  top: calc($header-height-mobile + 20px);
  right: 0;
  padding: 0 $container-padding;

  @include media-breakpoint-up(lg) {
    top: calc($header-height-desktop + 40px);
    right: calc(35px - $container-padding);
  }
}

.toast {
  position: relative;
  display: inline-block;
  width: auto;
  max-width: $toast-max-width;

  &__close-btn {
    position: absolute;
    top: $toast-padding-y;
    right: $toast-padding-x;
    z-index: 5;
    line-height: 1;

    + .toast-body {
      padding-right: 3.5625rem;
    }
  }

  &-body {
    padding: $toast-padding-y $toast-padding-x;
  }

  &.bg-success,
  &.bg-danger,
  &.bg-warning {
    .toast-body {
      padding-left: 3.5625rem;
    }

    &:before {
      position: absolute;
      top: $toast-padding-y;
      left: $toast-padding-x;
      z-index: 5;
      font-size: 20px;
      font-family: "icomoon";
      line-height: 1;
    }
  }

  &.bg-success {
    background-color: $blue-light !important;
    border-color: $white;

    &:before {
      color: $primary;
      content: "\e90c";
    }
  }

  &.bg-danger {
    color: $white;
    background-color: $red-faded !important;
    border-color: $danger;

    &:before {
      color: $white;
      content: "\e92d";
    }
  }

  &.bg-warning {
    background-color: $warning !important;
    border-color: darken($warning, 20%);

    &:before {
      content: "\e904";
    }
  }
}
