.dashboard {
  padding-top: $header-height-mobile;

  @include media-breakpoint-up(lg) {
    padding: $header-height-desktop 0 0 $side-menu-width;
  }

  &__main {
    min-height: calc(100dvh - $header-height-mobile);
    padding: 30px 0;
    overflow: hidden;

    @include media-breakpoint-up(lg) {
      min-height: calc(100dvh - $header-height-desktop);
      padding: 20px 0;
    }

    .container-fluid {
      @include media-breakpoint-up(lg) {
        padding-right: 15px;
        padding-left: 15px;
      }
    }
  }
}
