.accordion {
  &.sm {
    .accordion-button {
      padding: 0.75rem calc(0.625rem + 0.625rem + $accordion-icon-width) 0.75rem 0.625rem;
      font-size: 1rem;

      &:after {
        right: 0.625rem;
      }
    }

    .accordion-body {
      padding: 0.625rem 0.625rem 0.9375rem;
    }
  }

  &-item {
    border-top: $accordion-border-width solid $accordion-border-color !important;
    border-radius: $accordion-border-radius;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &-button {
    position: relative;
    padding-right: calc($accordion-padding-x + $accordion-padding-x + $accordion-icon-width);
    font-weight: $font-weight-bold;
    font-size: $font-size-sm;
    border-radius: $accordion-inner-border-radius;
    box-shadow: none !important;

    @include media-breakpoint-up(lg) {
      font-size: $font-size-lg;
    }

    &:hover {
      color: $primary;
    }

    &:after {
      position: absolute;
      top: 50%;
      right: $accordion-padding-x;
      z-index: 5;
      background-position: center;
      transform: translate(0, -50%);
    }
  }

  &-body {
    padding-top: 0;
  }
}
