.header {
  display: flex;
  align-items: center;
  min-height: $header-height-mobile;
  padding: 15px 0;
  background-color: $primary;

  @include media-breakpoint-up(lg) {
    left: $side-menu-width;
    min-height: $header-height-desktop;
    background-color: $blue-light;
  }

  &.fixed-top {
    transition: box-shadow 0.3s;

    &.active {
      box-shadow: 0 5px 10px rgba($secondary, 0.1);
    }
  }

  .container-fluid {
    @include media-breakpoint-up(lg) {
      padding-right: 45px;
      padding-left: 45px;
    }
  }

  &__logo {
    display: inline-flex;
    width: 100%;
    max-width: 160px;

    @include media-breakpoint-up(lg) {
      display: none;
    }
  }

  &__menu-toggler {
    @include btn-reset;

    display: inline-flex;
    color: $white;
    font-size: 20px;
  }
}
