.form-control {
  &.bordered-version {
    padding-top: 4px;
    padding-bottom: 4px;
    font-size: 16px;
    background-color: transparent;
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 0;

    @include media-breakpoint-up(lg) {
      font-size: 20px;
    }

    &.white-border {
      border-color: $white;
    }
  }

  &.with-arrow {
    padding-right: calc($input-padding-x + $input-padding-x + 10px);
    background-image: escape-svg($input-indicator);
    background-repeat: no-repeat;
    background-position: right $input-padding-x center;
    background-size: 10px 15px;

    &:focus,
    &:not(:placeholder-shown) {
      background-image: escape-svg($input-indicator-active);
    }
  }

  &.dark-version {
    @include placeholder {
      color: $secondary;
    }

    background-color: transparent;
    border-color: $secondary;
  }
}

.table {
  thead {
    th {
      .form-control {
        &.bordered-version {
          @include placeholder {
            font-weight: $font-weight-normal;
          }

          font-weight: $font-weight-bold;
        }
      }
    }
  }
}
