.btn {
  @include inline-flex-center;

  gap: 8px;

  // sizes
  &-sm {
    min-height: 37px;

    @include media-breakpoint-up(lg) {
      min-height: 42px;
    }

    @include media-breakpoint-down(lg) {
      padding-right: 10px;
      padding-left: 10px;
      font-size: 13px;
      border-radius: $border-radius-sm;
    }
  }

  &:not(.btn-sm, .btn-lg) {
    min-height: 56px;
  }

  &.equal-height-to-forms {
    min-height: $input-height;
  }

  [class^="icomoon-"] {
    font-size: 15px;

    @include media-breakpoint-up(lg) {
      font-size: 20px;
    }
  }

  .icomoon-close {
    @include media-breakpoint-up(lg) {
      font-size: 15px;
    }
  }

  .icomoon-triangle-down {
    @include media-breakpoint-up(lg) {
      font-size: 15px;
    }
  }

  .icomoon-chevron-down {
    font-size: 15px;
  }

  // colors
  &-blue-light {
    @include button-variant($blue-light, $blue-light, $secondary, darken($blue-light, 5%), darken($blue-light, 5%), $secondary, darken($blue-light, 5%), darken($blue-light, 5%), $secondary);
  }

  &-red {
    color: $white !important;
  }

  &-red-faded {
    color: $white !important;
  }

  &-link {
    font-weight: $btn-font-weight;
    text-decoration: none;
  }

  &-outline-blue-light {
    color: $secondary;

    &:hover {
      color: $primary;
    }
  }

  &-close {
    transition: color 0.3s;

    &.h1,
    &.h2,
    &.h3,
    &.h4,
    &.h5,
    &.h6 {
      margin-bottom: 0;
    }

    &:hover {
      color: $primary;
    }
  }

  &-drag-and-drop {
    @include size(15px);
    @include inline-flex-center;
    @include btn-reset;

    color: $blue-light;
    font-size: 14px;
    cursor: grabbing;
    transition: color 0.3s;

    &:hover {
      color: $primary;
      cursor: grabbing;
    }
  }

  &-gear {
    @include btn-reset;

    display: inline-flex;
    color: $white;
    font-weight: 400;
    font-size: 32px;
    transition: color 0.3s;

    &:hover {
      color: $blue-light;
    }

    &:after {
      content: none;
    }
  }

  &-edit {
    @include btn-reset;

    display: inline-flex;
    color: $blue-lake-ice;
    font-size: 15px;
    line-height: 1;
    transition: color 0.3s;

    @include media-breakpoint-up(sm) {
      font-size: 20px;
    }

    &:hover {
      color: $primary;
    }
  }

  &-remove {
    @include btn-reset;

    display: inline-flex;
    color: $secondary;
    font-size: 20px;
    line-height: 1;
    transition: color 0.3s;

    &:hover {
      color: $danger;
    }
  }

  &-action {
    @include btn-reset;
    @include size(30px);
    @include inline-flex-center;

    font-size: 20px;
    line-height: 1;
    transition: color 0.3s;

    &:hover {
      color: $primary;
    }
  }
}
