.side-menu {
  @include media-breakpoint-up(lg) {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: $zindex-fixed;
    width: $side-menu-width;
    color: $offcanvas-color;
    background-color: $offcanvas-bg-color !important;
  }

  @include media-breakpoint-down(sm) {
    width: 100vw !important;
  }

  &__header {
    display: flex;
    align-items: center;
    min-height: $header-height-mobile;
    padding: 10px $container-padding;
    background-color: $blue-light;

    @include media-breakpoint-up(lg) {
      min-height: $header-height-desktop;
      padding: 15px 30px;
      background-color: transparent;
    }
  }

  &__body {
    @extend %custom-scrollbar;

    max-height: calc(100dvh - $header-height-desktop);
    padding: 20px $container-padding;
    overflow: auto;

    @include media-breakpoint-up(lg) {
      padding: 15px 20px 15px 30px;
    }
  }

  &__logo {
    display: inline-flex;
    width: 100%;
    max-width: 190px;
  }

  &__nav {
    @include list-reset;
  }

  &__nav-item {
    &:not(:last-child) {
      padding-bottom: 5px;

      @include media-breakpoint-up(lg) {
        padding-bottom: 10px;
      }
    }

    &:first-child {
      .side-menu__nav-link {
        &.active {
          @include media-breakpoint-up(lg) {
            margin-top: 0;
          }
        }
      }
    }

    &:last-child {
      .side-menu__nav-link {
        &.active {
          @include media-breakpoint-up(lg) {
            margin-bottom: 0;
          }
        }
      }
    }
  }

  &__nav-link {
    position: relative;
    display: inline-flex;
    align-items: center;
    min-height: 56px;
    padding: 10px 10px 10px 50px;
    color: $white;
    text-decoration: none;
    border-radius: $border-radius;
    transition:
      background-color 0.3s,
      color 0.3s,
      padding-left 0.3s;

    @include media-breakpoint-up(lg) {
      display: flex;
      min-height: 62px;
      padding-left: 40px;
    }

    &:not(.active) {
      &:hover {
        @include media-breakpoint-up(lg) {
          padding-left: 60px;
          background-color: rgba($blue-light, 0.2);
        }

        .side-menu__nav-icon {
          @include media-breakpoint-up(lg) {
            left: 10px;
          }
        }
      }
    }

    &:before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: -$container-padding;
      z-index: 5;
      display: block;
      width: 5px;
      height: 100%;
      background-color: $blue-light;
      opacity: 0;
      transition: opacity 0.3s;
      content: "";

      @include media-breakpoint-up(lg) {
        left: -30px;
      }
    }

    &.active {
      padding-left: 60px;
      color: $secondary;
      background-color: $blue-light;

      @include media-breakpoint-up(lg) {
        margin: 15px 0;
        padding-left: 55px;
      }

      &:before {
        opacity: 1;
      }

      &:hover {
        background-color: darken($blue-light, 5%);
      }

      .side-menu__nav-icon {
        left: 10px;
        color: inherit;

        @include media-breakpoint-up(lg) {
          left: 15px;
        }
      }

      + .collapse,
      + .collapsing {
        .side-menu__sub-nav {
          @include media-breakpoint-up(lg) {
            padding-top: 10px;
          }
        }
      }
    }
  }

  &__nav-link-text {
    font-weight: 700;
    font-size: 20px;

    @include media-breakpoint-up(lg) {
      font-size: 16px;
    }
  }

  &__nav-icon {
    position: absolute;
    top: 50%;
    left: 0;
    z-index: 5;
    color: $blue-light;
    font-size: 40px;
    transform: translateY(-50%);
    transition: left 0.3s;

    @include media-breakpoint-up(lg) {
      font-size: 30px;
    }
  }

  &__sub-nav {
    @include list-reset;

    padding: 15px 0 15px 30px;

    @include media-breakpoint-up(lg) {
      padding: 25px 0 25px 30px;
    }
  }

  &__sub-nav-item {
    &:not(:last-child) {
      margin-bottom: 5px;

      @include media-breakpoint-up(lg) {
        margin-bottom: 10px;
      }
    }
  }

  &__sub-nav-link {
    display: inline-flex;
    padding: 10px 0;
    color: $white;
    font-weight: $font-weight-bold;
    font-size: 16px;
    text-decoration: none;
    border-bottom: $border-width $border-style transparent;
    transition: color 0.3s;

    @include media-breakpoint-up(lg) {
      font-size: $font-size-sm;
    }

    &:hover {
      color: darken($blue-light, 5%);
    }

    &.active {
      border-bottom-color: $border-color;
    }
  }
}
