.account-toggler {
  @include btn-reset;

  position: relative;
  display: inline-flex;
  color: $secondary;

  @include media-breakpoint-up(lg) {
    padding-right: 35px;
  }

  &:hover {
    color: $primary;
  }

  &:after {
    position: absolute;
    top: 50%;
    right: 0;
    margin: 0;
    font-size: 15px;
    font-family: "icomoon";
    border: none;
    transform: translate(0, -50%);
    transition: color 0.3s;
    content: none;

    @include media-breakpoint-up(lg) {
      content: "\e901";
    }
  }

  &__avatar {
    @include size(28px);

    position: relative;
    display: inline-flex;
    overflow: hidden;
  }
}
