.uploaded-file {
  @include responsive-ratio(220, 130);

  position: relative;
  background-color: $gray;

  &__delete-btn {
    @include btn-reset;
    @include size(22px);
    @include inline-flex-center;

    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    font-weight: $font-weight-bold;
    font-size: 15px;
    background-color: $white;
    border: 1px solid $blue-light;
    border-radius: 50rem;
    transform: translate(50%, -50%);
    transition: color 0.3s;

    &:hover {
      color: $danger;
    }
  }
}
