.form-check {
  &:not(.form-switch) {
    display: inline-block;
    cursor: $form-check-label-cursor;

    @include media-breakpoint-down(sm) {
      padding-left: 20px;
    }

    &.sm {
      padding-left: 18px;

      .form-check-input {
        @include size(10px);

        margin-top: 5px;
        margin-left: -18px;
      }

      .form-check-label {
        font-size: 14px;
      }
    }

    .form-check-input {
      cursor: $form-check-label-cursor;
      transition:
        border-color 0.3s,
        background-color 0.3s;

      @include media-breakpoint-down(sm) {
        width: 12px;
        height: 12px;
        margin-top: 0.25rem;
        margin-left: -1.25rem;
      }

      &[type="checkbox"] {
        background-size: 80%;

        &:checked {
          border-color: $form-check-input-checked-bg-color;
        }
      }

      &[type="radio"] {
        &:checked {
          background-color: transparent;
        }
      }
    }

    .form-check-label {
      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }
  }
}

.radio-pills-list {
  display: flex;
  align-items: center;

  .radio-pill {
    &:first-child {
      .radio-pill__item {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
    }

    &:last-child {
      .radio-pill__item {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }
    }

    &:not(:first-child, :last-child) {
      .radio-pill__item {
        border-radius: 0;
      }
    }

    + .radio-pill {
      margin-left: -$border-width;
    }
  }
}

.radio-pill {
  position: relative;
  display: inline-flex;

  &:hover {
    .radio-pill__input {
      &:not(:checked) {
        ~ .radio-pill__item {
          color: $primary;
        }
      }
    }
  }

  &__input {
    @include visually-hidden;

    &:checked {
      ~ .radio-pill__item {
        background-color: $border-color;
      }
    }
  }

  &__item {
    @include inline-flex-center;

    flex-wrap: nowrap;
    gap: 8px;
    min-width: 120px;
    min-height: 30px;
    padding: 2px 10px;
    color: $secondary;
    font-size: 13px;
    white-space: nowrap;
    text-align: center;
    background-color: $white;
    border: $border-width $border-style $border-color;
    border-radius: 50rem;
    cursor: pointer;
    transition:
      color 0.3s,
      background-color 0.3s;

    @include media-breakpoint-up(lg) {
      font-size: 16px;
    }

    [class*="icomoon"] {
      font-size: 0.75em;
    }
  }
}
