label {
  &.form-label {
    cursor: pointer;
  }
}

.form-label {
  display: inline-block;

  [data-bs-toggle="tooltip"] {
    font-size: 8px;
  }
}

.form-control {
  caret-color: $input-color;
  text-overflow: ellipsis;

  &[disabled] {
    -webkit-text-fill-color: $input-disabled-color;
    cursor: not-allowed;
  }

  &.is-valid,
  &.is-invalid {
    &:focus {
      box-shadow: none;
    }
  }
}

textarea {
  &.form-control {
    height: 95px;

    @include media-breakpoint-up(sm) {
      height: 125px;
    }
  }
}

.form-select {
  &:not([disabled]) {
    cursor: pointer;
  }

  &[disabled] {
    cursor: not-allowed;
  }
}

.position-relative {
  > .invalid-feedback {
    position: absolute;
    top: calc(100% + $form-label-margin-bottom);
    left: 0;
    z-index: 5;
    margin-top: 0;
  }
}
