.of-contain {
  @include absolute-cover;

  object-fit: contain;
}

.of-cover {
  @include absolute-cover;

  object-fit: cover;
}

%custom-scrollbar {
  @include custom-scrollbar(6px, $blue-light, $primary, 6px);
}

%custom-scrollbar-thin {
  @include custom-scrollbar(2px, transparent, $primary, 2px);
}

.custom-scrollbar {
  @extend %custom-scrollbar;
}

.custom-scrollbar-thin {
  @extend %custom-scrollbar-thin;
}

.tabular-nums {
  font-variant-numeric: tabular-nums;
}

.resize-animation-stopper * {
  transition: none !important;
}

.border-right-not-last {
  [class^="col"] {
    &:not(:last-child) {
      border-right: $border-width $border-style rgba($primary, 0.4);
    }
  }
}

.position-sm-static {
  @include media-breakpoint-up(sm) {
    position: static !important;
  }
}
