.card {
  width: 100%;
  padding: $card-spacer-y $card-spacer-x;

  &.wrapper {
    @include media-breakpoint-down(lg) {
      padding: 0;
      background-color: transparent;
    }
  }

  &-edit {
    width: 100%;
    padding: 20px;
    background-color: $card-bg;
    border-radius: $card-border-radius;

    @include media-breakpoint-up(sm) {
      min-height: 168px;
    }

    @include media-breakpoint-up(lg) {
      padding: 45px $card-spacer-x;
    }

    &__content {
      flex-grow: 1;
    }
  }

  &-owner {
    width: 100%;
    padding: 20px;
    background-color: $card-bg;
    border-radius: $card-border-radius;

    @include media-breakpoint-up(sm) {
      padding: 50px 30px;
    }

    &__photo {
      @include size(67px);

      position: relative;
      overflow: hidden;
      border: 1px solid $blue-light;
      border-radius: 50rem;
    }
  }

  &-integration {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 50px 20px;
    text-align: center;
    background-color: $blue-light;

    &__top-content {
      flex-grow: 1;
      padding-bottom: 30px;
    }

    &__logo {
      position: relative;
      display: inline-flex;
      width: 100px;
      height: 20px;
      margin-bottom: 30px;
      overflow: hidden;
    }
  }
}
