.icon-link {
  text-decoration: none;
  transition: color 0.3s;

  @include media-breakpoint-down(lg) {
    font-size: 13px;
  }
}

a {
  text-underline-offset: 2px;
  text-decoration-thickness: 1px;
}

.link {
  &-primary {
    transition: color 0.3s;

    &:hover {
      color: $secondary !important;
    }
  }

  &-secondary {
    transition: color 0.3s;

    &:hover {
      color: $primary !important;
    }
  }

  &-danger {
    transition: color 0.3s;

    &:hover {
      color: darken($danger, 10%) !important;
    }
  }

  &-gray {
    transition: color 0.3s;

    &:hover {
      color: $secondary !important;
    }
  }

  &-blue-lake-ice {
    transition: color 0.3s;

    &:hover {
      color: $primary !important;
    }
  }

  &-with-arrow {
    position: relative;
    display: inline-flex;
    padding-right: 1em;
    text-decoration: none;

    &:after {
      position: absolute;
      top: 50%;
      right: -0.3em;
      z-index: 5;
      font-size: 0.6em;
      font-family: "icomoon";
      transform: translate(0, -50%);
      content: "\e903";
    }

    &.left-side {
      padding-right: 0;
      padding-left: 1em;

      &:after {
        right: auto;
        left: -0.3em;
      }
    }
  }
}
