.dropdown {
  &-item {
    font-weight: $font-weight-bold;
    transition:
      color 0.3s,
      background-color 0.3s;
  }

  &-menu {
    @extend %custom-scrollbar-thin;

    max-height: 285px;
    overflow: auto;

    .btn-sm {
      min-height: 38px;
      padding-right: 15px;
      padding-left: 15px;
    }

    &.table-ordering {
      max-height: none;

      .dropdown-menu__content {
        @extend %custom-scrollbar-thin;

        max-height: 285px;
        padding: 0 $dropdown-padding-y;
        overflow: auto;
      }

      .dropdown-menu__footer {
        padding: $dropdown-padding-y $dropdown-padding-y 0;
      }
    }

    &.sm {
      min-width: 125px;
      font-size: 14px;
    }
  }
}
