.table-responsive {
  @extend %custom-scrollbar;

  @include media-breakpoint-down(sm) {
    max-height: 500px !important;
  }
}

.table {
  margin-bottom: 0;
  font-size: $font-size-lg;
  line-height: $line-height-lg;

  @include media-breakpoint-down(lg) {
    font-size: 14px;
  }

  > thead {
    vertical-align: middle;

    > tr {
      &:first-child {
        > th {
          position: sticky;
          top: 0;
          z-index: 100;
          min-width: 230px;
          height: 55px;
          padding-top: 10px;
          padding-bottom: 10px;
          color: $white;
          white-space: nowrap;
          background-color: $primary;

          @include media-breakpoint-down(lg) {
            font-size: 18px;
          }

          &::selection {
            background-color: $blue-lake-ice;
          }

          &.with-sorting {
            cursor: pointer;
            transition: background-color 0.3s;

            &:hover {
              background-color: lighten($primary, 5%);
            }

            [class*="icomoon-"] {
              margin-left: 10px;
              font-size: 14px;
            }
          }

          &.with-gear {
            min-width: 135px;
          }

          &:first-child {
            border-top-left-radius: $border-radius-lg;
          }

          &:last-child {
            border-top-right-radius: $border-radius-lg;
          }
        }
      }

      &:nth-child(2) {
        > th {
          position: sticky;
          top: 55px;
          z-index: 100;
          padding-top: 15px;
          padding-bottom: 15px;

          &:after {
            position: absolute;
            right: 0;
            bottom: 0;
            left: 0;
            display: block;
            height: $border-width;
            background-color: $border-color;
            content: "";
          }
        }
      }
    }
  }

  > tbody {
    tr {
      &.table-active {
        .name-item {
          &__round-badge {
            outline: 1px solid $white !important;
          }
        }

        .badge {
          &.bg-blue-light {
            outline: 1px solid $white !important;
          }
        }

        .btn {
          &-outline-blue-light {
            outline: 1px solid $white !important;
          }
        }
      }
    }
  }

  &.table-striped > tbody > tr:nth-of-type(#{$table-striped-order}) > * {
    .name-item {
      &__round-badge {
        outline: 1px solid $white !important;
      }
    }

    .badge {
      &.bg-blue-light {
        outline: 1px solid $white !important;
      }
    }

    .btn {
      &-outline-blue-light {
        outline: 1px solid $white !important;
      }
    }
  }
}

.table-footer {
  padding-top: 30px;
}
